@charset "utf-8";
/* _variables.scss */
// PSD사이즈 나누기 2
@function x2($data){
	@return round($data / 2) + px;
}
@function rem($value){ // ex = vw(44vw)
	@return ($value / ($windowSize * 0.01)) + vw;
}
$windowSize : 640;

//root path
//$G-path: '../';
$G-width:      1200px;  // 사이트 컨텐츠 크기
$G-maxWidth:   1920px;  // 디자안 최대크기
$G-wMobile    : 768px;  // 미디어쿼리 모바일
$G-minWidth   : 320px;  // 최소 모바일 

//img path
$Gpath-img:  '../images/';

//body color
//$G-body-color: #2f2d2d;

