@charset "utf-8";
html, body { -webkit-text-size-adjust:none;  }  /*글자자동조절방지*/
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,font,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td {margin:0;padding:0;border:0;}
ul, li, ol { list-style:none;  }
img, fieldset , video{ border:0;  vertical-align:top; }
input, textarea, select { vertical-align:middle; resize:none; position:relative; border-radius:0; }	
button { border:0 none; background:transparent; cursor:pointer; }		
caption, legend, .hide  { position:absolute; top:0; left:0; width:0; height:0; font-size:0; line-height:0; overflow:hidden; text-indent:-9999px; }
table { border-collapse:collapse; border-spacing:0; line-height:1.5}
table caption.tit { width:100%; height:auto; margin:20px 0; font-size:20px; font-weight:bold; text-indent:0; visibility:visible}
a:link, a:visited  { text-decoration:none; color:#333; }
a:active, a:hover, a:focus { text-decoration:none; }		
ins{text-decoration:none}
del{text-decoration:line-through}
/* HTML5 display-role reset for older browsers   */	
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display:block; box-sizing: border-box; }
blockquote, q { quotes:none; }
blockquote:before, blockquote:after, q:before, q:after { content:''; content:none; }
body, a, li, dt, dd, td, th, span, div, input, textarea,  button, option, 
h1, h2, h3, h4, h5, h6, legend, figcaption { padding: 0;
    font-family: $baseFT, 'android sans fallback', 'AppleGothic', "Nanum Gothic",  Malgun Gothic, dotum,"돋움",Arial,Sans-serif; line-height: 1.4;
	font-size: x2(28); vertical-align:middle; color: #666;
	box-sizing: border-box; }
i, em { font-style: normal; }
textarea { border:1px solid #cccccc;}

/* margin, padding (5px 단위로 100px까지) mg0, mg5, mg10 */
$trpClassName     : mg mt mb ml mr pd pt pb pl pr;
$trpAttributeName : margin margin-top margin-bottom margin-left margin-right
                 padding padding-top padding-bottom padding-left padding-right;    

@mixin trpAttrSet($class, $atrribute){
    @for $j from 0 through 2  {
        .#{$class}#{5 * $j} { #{$atrribute} : (5 * $j)+px !important;}
    } 
}

@for $i from 1 through length($trpClassName)  {
    @include trpAttrSet( #{nth($trpClassName , $i)} , #{nth($trpAttributeName , $i)} );  
}

/* ========================== layout */
.col1, .col2, .col3 { &:after { content: ''; display: block; clear: both} }
.col1>a, .col1>li , .col1>.colitem { float:left; box-sizing:border-box; width: 100%; }
.col2>a, .col2>li , .col2>.colitem { float:left; box-sizing:border-box; width: 50%; }
.col3>a, .col3>li , .col3>.colitem { float:left; box-sizing:border-box; width: 33.3333%; }
