// how to use : Use following mixins.
// variable : [X-axis] [Y-axis] [offset_x] [offset_y] [width] [height] [total_width] [total_height] [image_url] [image_name] [folder_name]
    $accordion_arrow_down: 50px 66px -50px -66px 30px 30px 202px 186px '../images/common/sp_icons.png' 'accordion_arrow_down' 'sp_icons';
    $accordion_arrow_up: 132px 0px -132px 0px 30px 30px 202px 186px '../images/common/sp_icons.png' 'accordion_arrow_up' 'sp_icons';
    $btn_del: 132px 50px -132px -50px 29px 29px 202px 186px '../images/common/sp_icons.png' 'btn_del' 'sp_icons';
    $btn_minus: 0px 0px 0px 0px 46px 46px 202px 186px '../images/common/sp_icons.png' 'btn_minus' 'sp_icons';
    $btn_plus: 66px 0px -66px 0px 46px 46px 202px 186px '../images/common/sp_icons.png' 'btn_plus' 'sp_icons';
    $btn_search: 0px 66px 0px -66px 30px 32px 202px 186px '../images/common/sp_icons.png' 'btn_search' 'sp_icons';
    $icon-page_next-on: 80px 166px -80px -166px 20px 20px 202px 186px '../images/common/sp_icons.png' 'icon-page_next-on' 'sp_icons';
    $icon-page_next: 40px 166px -40px -166px 20px 20px 202px 186px '../images/common/sp_icons.png' 'icon-page_next' 'sp_icons';
    $icon-page_nextnext-on: 120px 166px -120px -166px 20px 20px 202px 186px '../images/common/sp_icons.png' 'icon-page_nextnext-on' 'sp_icons';
    $icon-page_nextnext: 182px 0px -182px 0px 20px 20px 202px 186px '../images/common/sp_icons.png' 'icon-page_nextnext' 'sp_icons';
    $icon-page_prev-on: 182px 40px -182px -40px 20px 20px 202px 186px '../images/common/sp_icons.png' 'icon-page_prev-on' 'sp_icons';
    $icon-page_prev: 182px 80px -182px -80px 20px 20px 202px 186px '../images/common/sp_icons.png' 'icon-page_prev' 'sp_icons';
    $icon-page_prevprev-on: 182px 120px -182px -120px 20px 20px 202px 186px '../images/common/sp_icons.png' 'icon-page_prevprev-on' 'sp_icons';
    $icon-page_prevprev: 0px 166px 0px -166px 20px 20px 202px 186px '../images/common/sp_icons.png' 'icon-page_prevprev' 'sp_icons';
    $icon-pop_close: 52px 118px -52px -118px 28px 28px 202px 186px '../images/common/sp_icons.png' 'icon-pop_close' 'sp_icons';
    $icon_back_btn: 0px 118px 0px -118px 32px 28px 202px 186px '../images/common/sp_icons.png' 'icon_back_btn' 'sp_icons';
    $pop_close: 100px 118px -100px -118px 28px 28px 202px 186px '../images/common/sp_icons.png' 'pop_close' 'sp_icons';

//ancestor
    $sp_icons-width: 202px;
    $sp_icons-height: 186px;
    $sp_icons-image: '../images/common/sp_icons.png';
    $sp_icons-sprites: ($accordion_arrow_down, $accordion_arrow_up, $btn_del, $btn_minus, $btn_plus, $btn_search, $icon-page_next-on, $icon-page_next, $icon-page_nextnext-on, $icon-page_nextnext, $icon-page_prev-on, $icon-page_prev, $icon-page_prevprev-on, $icon-page_prevprev, $icon-pop_close, $icon_back_btn, $pop_close, );
    $sp_icons_sprites_name: (accordion_arrow_down, accordion_arrow_up, btn_del, btn_minus, btn_plus, btn_search, icon-page_next-on, icon-page_next, icon-page_nextnext-on, icon-page_nextnext, icon-page_prev-on, icon-page_prev, icon-page_prevprev-on, icon-page_prevprev, icon-pop_close, icon_back_btn, pop_close, );
    $sp_icons: (202px, 186px, '../images/common/sp_icons.png', $sp_icons-sprites, );

//mixin

        %sp_icons {
        background-image: url('../images/common/sp_icons.png');
        $sprite-background-size-x: 202px / 2;
        $sprite-background-size-y: 186px / 2;
        -webkit-background-size: $sprite-background-size-x $sprite-background-size-y;
        background-size: $sprite-background-size-x $sprite-background-size-y;
        }

        @mixin sp_icons($sprite) {
        @extend %sp_icons;
        @include spriteSize($sprite);
        @include spriteBackgroundPosition($sprite);
        }
