/* ====== 버튼 ====== */
.btn {  position: relative; box-sizing: border-box; border-radius: x2(10);
    display: inline-block; padding:x2(18) x2(33) x2(22); 
    margin-bottom: 0;
    font-weight: bold;line-height: 1.42857143;text-align: center;
    letter-spacing: -1px; white-space: nowrap; vertical-align: middle;
    -ms-touch-action: manipulation;touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;-moz-user-select: none;-ms-user-select: none;
    user-select: none; background-image: none;
    border: 1px solid transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
}

// 비활성화
.btn:disabled
{ @include opacity(0.5); pointer-events:none; }

