// how to use : Use following mixins.
// variable : [X-axis] [Y-axis] [offset_x] [offset_y] [width] [height] [total_width] [total_height] [image_url] [image_name] [folder_name]
    $checkbox-dim: 120px 49px -120px -49px 20px 20px 149px 140px '../images/common/sp_form.png' 'checkbox-dim' 'sp_form';
    $checkbox-off: 0px 0px 0px 0px 40px 40px 149px 140px '../images/common/sp_form.png' 'checkbox-off' 'sp_form';
    $checkbox-on: 60px 0px -60px 0px 40px 40px 149px 140px '../images/common/sp_form.png' 'checkbox-on' 'sp_form';
    $icon-input-x: 120px 0px -120px 0px 29px 29px 149px 140px '../images/common/sp_form.png' 'icon-input-x' 'sp_form';
    $radio-dim: 0px 120px 0px -120px 20px 20px 149px 140px '../images/common/sp_form.png' 'radio-dim' 'sp_form';
    $radio-off: 0px 60px 0px -60px 40px 40px 149px 140px '../images/common/sp_form.png' 'radio-off' 'sp_form';
    $radio-on: 60px 60px -60px -60px 40px 40px 149px 140px '../images/common/sp_form.png' 'radio-on' 'sp_form';

//ancestor
    $sp_form-width: 149px;
    $sp_form-height: 140px;
    $sp_form-image: '../images/common/sp_form.png';
    $sp_form-sprites: ($checkbox-dim, $checkbox-off, $checkbox-on, $icon-input-x, $radio-dim, $radio-off, $radio-on, );
    $sp_form_sprites_name: (checkbox-dim, checkbox-off, checkbox-on, icon-input-x, radio-dim, radio-off, radio-on, );
    $sp_form: (149px, 140px, '../images/common/sp_form.png', $sp_form-sprites, );

//mixin

        %sp_form {
        background-image: url('../images/common/sp_form.png');
        $sprite-background-size-x: 149px / 2;
        $sprite-background-size-y: 140px / 2;
        -webkit-background-size: $sprite-background-size-x $sprite-background-size-y;
        background-size: $sprite-background-size-x $sprite-background-size-y;
        }

        @mixin sp_form($sprite) {
        @extend %sp_form;
        @include spriteSize($sprite);
        @include spriteBackgroundPosition($sprite);
        }
