/* ======================== input text  */
/* placeholder */
// ie
input[type="text"].placeholdersjs,input[type="password"].placeholdersjs,input[type="number"].placeholdersjs,
textarea.placeholdersjs { color: $colorS; }
// Internet Explorer 10 이상에 적용
input[type="text"]:-ms-input-placeholder,input[type="password"]:-ms-input-placeholder,input[type="number"].placeholdersjs,
textarea:-ms-input-placeholder{ color: $colorS; }
//  Webkit, Blink, Edge에 적용
input[type="text"]::-webkit-input-placeholder ,input[type="password"]::-webkit-input-placeholder,input[type="number"].placeholdersjs,
textarea::-webkit-input-placeholder { color: $colorS; } 
// Mozilla Firefox 4 부터 18에 적용
input[type="text"]::-moz-placeholder,input[type="password"]::-moz-placeholder,input[type="number"].placeholdersjs,
textarea::-moz-placeholder { color: $colorS; } 

// input style
em.star { display: inline-block; height: x2(28); padding: 0 x2(10); color: #f24848; font-weight: bold; }
input[type="text"], input[type="password"], input[type="number"],
input[type="email"] {
    width: 100%; height: $formH; padding: x2(16) x2(28); border-radius: x2(10);
    font-size: x2(26); vertical-align: middle;
    border: 1px solid #d0d0d0; outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
}

/* ======================== focus, ,disabled, input_error */
input[type="text"]:focus,input[type="password"]:focus,
input[type="email"]:focus{ border-color: $colorL; }

input[type="text"]:disabled,input[type="password"]:disabled,
input[type="email"]:disabled{ background  : $colorBGd; }

input[type="text"]:read-only,input[type="password"]:read-only,
input[type="email"]:read-only  { background  : $colorBGd; }

input[type="text"].input_error,input[type="password"].input_error,
input[type="email"].input_error{ border-color: $colorE; }

input[readonly] { background  : $colorD; } //ie적용
.input-box{ position: relative;  vertical-align: top;
    .check_byte-w { padding-right: x2(180) !important; }
    .check_byte-h { padding-bottom: x2(100) !important; }
    label, .dash  { display: inline-block; 
        padding: x2(16) x2(40); ; box-sizing: border-box; }
    label:frist-child { padding-left: 0;  }
    .dash { padding: x2(16) 0 x2(16); } 
    input[type="text"], input[type="password"] {
       padding: x2(16) x2(28);
    }
    .btn { margin-top: x2(-2);}
    .byte_check { position: absolute; top: x2(20); right: x2(30);
        em   { vertical-align: middle; font-size: x2(24);  color: #e90000; }    
        span { vertical-align: middle;  font-size: x2(24); }
    }
}
.msg { display:block; margin: x2(20) 0; color:$colorS; font-size:x2(24);}
.complete { margin: x2(20) 0; color:$colorE; font-size:x2(24);}

/* input 삭제 */
.input_del { position: relative;
    input { padding-right: x2(50); }
    button { position: relative;  display: none; position: absolute; top:50%; right: 0; margin-top: x2(-34); padding: x2(20); font-size: 0;
        &:after { content: ""; display: block; @include sp_form($icon-input-x); 
        }
    }
}

/* ======================== textarea  */
.textarea-box { border: 1px solid #d0d0d0; padding: x2(16) x2(28); border-radius: x2(10);
    textarea { width: 100%;  height: x2(200); border: 0; color:#888; font-size: x2(26);
        -webkit-appearance: none;
        -moz-appearance: none;    
    }
}

/* ========================  input radio */
.trp.radio-box { position: relative; display: inline-block; border:1px solid #d0d0d0; border-radius: x2(10); padding: x2(18) x2(10);/* padding-left: 27px;  */ min-height: x2(40);
    label { display: inline-block; font-size: x2(26); line-height: x2(40); margin-left: x2(10); vertical-align: top; }
    em { display: inline-block; height: x2(40);    text-indent: 0px; }
    input[type="radio"] { position: absolute; top:0; left: 0; /*  width:100%; */ width: x2(40); height: x2(40); margin: 0; padding: 0;  opacity: 0; filter: alpha(opacity=0);  }    
    input[type="radio"] + i { display: inline-block; vertical-align: top;  }
    input[type="radio"] + i:after { display: block; content: '';  @include sp_form($radio-off); }
    input[type="radio"]:checked + i:after {  @include sp_form($radio-on); }
    input[type="radio"]:disabled + i      {   @include opacity(0.5);
        &:after { display: block; content: '';  /* @include sp_form($radio-dim); */ }
    }
    input[type="radio"]:disabled + i +label  {  @include opacity(0.5);/*  color:$colorP; */ }

    .bg_color { vertical-align: top; margin-top: x2(-6);  margin-left: x2(20);
        display: inline-block; width: x2(140); height: x2(60); }
    //.bg_color.color1 { background-color: #000; }
}
.trp.radio-box.re { 
    label { display: inline-block; height: x2(40); vertical-align: middle;  text-indent: 0px; }
    input[type="radio"] + i { position: relative;  }
}

/* ========================  input checkbox */
.trp.checkbox-box { position: relative; display: inline-block;  min-height: x2(40); 
    label { display: inline-block; line-height: x2(40); margin-left: x2(6);  vertical-align: top; cursor: pointer;  }
    em { display: inline-block; height: x2(40);     text-indent: 0px;}
    input[type="checkbox"] { position: absolute; top:0; left: 0;  /*  width:100%; */ width: x2(40); height: x2(40); margin: 0; padding: 0;  opacity: 0; filter: alpha(opacity=0); z-index: 1; }
    input[type="checkbox"] + i { display: inline-block; vertical-align: top; margin-top:0px; cursor: pointer;  }
    input[type="checkbox"] + i:after { display: block; content: ''; width: x2(40); height: x2(40);  @include sp_form($checkbox-off); }
    input[type="checkbox"]:checked + i:after {  @include sp_form($checkbox-on); }
    input[type="checkbox"]:disabled + i      {  @include opacity(0.5);
        &:after { display: block; content: '';  /* @include sp_form($checkbox-dim); */ }
    }
    input[type="checkbox"]:disabled + i +label  { @include opacity(0.5);  /* color:$colorP; */ }

    .bg_color { vertical-align: top; margin-top: x2(-12);  margin-left: x2(20);
        display: inline-block; width: x2(95); height: x2(66); }
    //.bg_color.color1 { background-color: #000; }
}
.trp.checkbox-box.re {
    label { display: inline-block; height: x2(40); text-indent: 0px; }
    input[type="checkbox"] + i { position: relative;  }
}